import React from 'react';
import { GrFormAdd, GrFormSubtract } from 'react-icons/gr';
import { useOrder } from '../../services/order.service';

export const OrderControls = ({ className, product, pizzaPrice, duplicateItemId = null }) => {
  const { addProductToCart, removeProductFromCart, duplicateCartItem } = useOrder();

  const handleAddProduct = (product, pizzaPrice) => {
    if (duplicateItemId) {
      duplicateCartItem(duplicateItemId);
      return;
    }

    addProductToCart({ product, pizzaPrice });
  };

  const handleRemoveProduct = (product, pizzaPrice) => {
    if (duplicateItemId) {
      // console.log(product);
      removeProductFromCart({ pizzaId: product.pizzaId, pizzaPriceId: product.pizzaPriceId });
      return;
    }
    removeProductFromCart({ pizzaId: product.id, pizzaPriceId: pizzaPrice.id });
  };
  return (
    <div className={`orderControls ${className}`}>
      <div
        className="orderControls__add"
        onClick={() => handleRemoveProduct(product, pizzaPrice)}>
        <GrFormSubtract />
      </div>
      <div
        className="orderControls__remove"
        onClick={() => handleAddProduct(product, pizzaPrice)}>
        <GrFormAdd />
      </div>
    </div>
  );
};
