import { default as React, useContext, createContext } from 'react';

import { apiConfig } from '../config';

const ApiContext = createContext();
const useApi = () => useContext(ApiContext);

const ApiProvider = ({ children }) => {
  const BASE_URL = `${apiConfig.baseURL}`;

  const getAllProducts = async (embed = false, categoryId = null) => {
    let params = [];

    if (embed) params.push('_embed');
    
    const allCategoriesParams = [null, -1, '-1'];
    if (!allCategoriesParams.includes(categoryId))
      params.push(`categories=${categoryId}`);

    const query = params.length > 0 ? `?${params.join('&')}` : '';

    let url = `${BASE_URL}/pizza${query}`;

    const response = await fetch(url);
    return response.json();
  };

  const getAllCategories = async () => {
    let url = `${BASE_URL}/categories`;
    const response = await fetch(url);
    return response.json();
  };

  const getAllOrdersFromUser = async (userId, embed = false) => {
    let url = `${BASE_URL}/purchase?author=${userId}`;
    // if (query !== null) {
    //   url += (url.indexOf('?') === -1 ? '?' : '&') + queryParams(query);
    // }
    const response = await fetch(url);
    return response.json();
  };

  const getPizzaPricesByIds = async (ids, embed = false) => {
    const formattedIds = ids.join(',');
    let url = `${BASE_URL}/pizza_price?include=${formattedIds}${
      embed ? '&_embed' : ''
    }`;
    const response = await fetch(url);
    return response.json();
  };

  // const registerUser = async (username = 'testuser', email = 'testuser@testttt.com', password = "123456") => {
  const registerUser = async (username, email, password) => {
    let url = `${BASE_URL}/users/register`;
    const body = {
      username,
      email,
      password,
    };
    const options = {
      method: 'post',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(body),
    };

    const response = await fetch(url, options);
    return response.json();
  };

  const createCustomerData = async ({
    username,
    firstname,
    lastname,
    postalcode,
    street,
    housenumber,
    customer,
  }) => {
    let url = `${BASE_URL}/customer_data`;
    const body = {
      title: `${username}`,
      status: 'publish',
      fields: {
        firstname,
        lastname,
        postalcode,
        street,
        housenumber,
        customer,
      },
    };

    const authToken = JSON.parse(localStorage.getItem('credentials')).token;
    const options = {
      method: 'post',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        Authorization: `Bearer ${authToken}`,
      },
      body: JSON.stringify(body),
    };

    const response = await fetch(url, options);
    return response.json();
  };

  const getCustomerData = async (customerId) => {
    const url = `${BASE_URL}/customer_data?customer=${customerId}`;
    const response = await fetch(url);
    return response.json();
  };

  const getCurrentUserAccount = async (authToken) => {
    const url = `${BASE_URL}/users/me`;
    let token =
      authToken || JSON.parse(localStorage.getItem('credentials')).token;
    const options = {
      method: 'get',
      headers: {
        Accept: 'application/json',
        // 'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`,
      },
    };
    const response = await fetch(url, options);
    return response.json();
  };

  const generateJWTToken = async (username, password) => {
    let url = `${apiConfig.JWTURL}`;
    const body = {
      username,
      password,
    };
    const options = {
      method: 'post',
      mode: 'cors',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(body),
    };

    const response = await fetch(url, options);
    let data = await response.json();
    const userData = await getCurrentUserAccount(data.token);
    data.user_id = userData.id;

    if (response) {
      localStorage.setItem('credentials', JSON.stringify(data));
    }
    return data;
  };

  const postOrder = async ({
    customer_data,
    purchase,
    total,
    address,
    delivery_time,
  }) => {
    let url = `${BASE_URL}/purchase`;

    const { token } = JSON.parse(localStorage.getItem('credentials'));

    const body = {
      title: `${address} (Bestelling via app)`,
      status: 'publish',
      fields: {
        customer_data,
        purchase,
        total,
        address,
        delivery_time,
      },
    };

    const options = {
      method: 'post',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(body),
    };

    const response = await fetch(url, options);
    return response.json();
  };

  // const deleteCurrentUser = async () => {
  //   const url = `${BASE_URL}/users/me?reassign=1`;

  //   const { token } = JSON.parse(localStorage.getItem('credentials'));
    
  //   const options = {
  //     method: 'delete',
  //     headers: {
  //       Accept: 'application/json',
  //       // 'Content-Type': 'application/json',
  //       Authorization: `Bearer ${token}`,
  //     },
  //   };

  //   const response = await fetch(url, options);
  //   console.log(await response.json());
  //   return response.json();
  // };

  // const getMedia = async (mediaId) => {
  //   let url = `${BASE_URL}/media/${mediaId}`;

  //   const response = await fetch(url);
  //   return response.json();
  // };

  // const queryParams = (options) => {
  //   return Object.keys(options)
  //     .map(key => encodeURIComponent(key) + '=' + encodeURIComponent(options[key])).join('&');
  // }

  const exports = {
    getAllProducts,
    getAllCategories,
    getPizzaPricesByIds,
    registerUser,
    createCustomerData,
    generateJWTToken,
    getAllOrdersFromUser,
    getCustomerData,
    postOrder,
    // deleteCurrentUser,
    // getMedia,
  };

  return <ApiContext.Provider value={exports}>{children}</ApiContext.Provider>;
};

export { ApiContext, ApiProvider, useApi };
