import React, { useEffect, useState } from 'react';
import { useApi, useModal } from '../../services';
import { GrFormClose } from 'react-icons/gr';
import { useOrder } from '../../services/order.service';
import { OrderControls } from '../Controls';

export const ProductModal = ({ product }) => {
  const { clearModal } = useModal();
  const { currentOrder } = useOrder();
  const { getPizzaPricesByIds } = useApi();

  const productName = product?.title?.rendered || '';
  const productImgSrc =
    product?._embedded['wp:featuredmedia'][0].source_url || '';
  const productIngredients = product?.acf?.ingredients || '';
  //   const productSizes = product?._embedded['wp:term'][1] || [];
  const [productPrices, setProductPrices] = useState([]);
  const [productInCart, setProductInCart] = useState({});

  useEffect(() => {
    const thisProductInCart = currentOrder.fields.pizzas.filter(
      (pizza) => pizza.pizzaId === product.id
    );
    setProductInCart(thisProductInCart);
  }, [currentOrder, product.id]);

  useEffect(() => {
    // if (!productSizes) return;
    (async () => {
      const pizzaPriceIds = product.acf.prices.map((price) => price.ID);
      const pizzaPrices = await getPizzaPricesByIds(pizzaPriceIds, true);
      setProductPrices(pizzaPrices);
    })();
  }, [product, getPizzaPricesByIds]);

  return (
    <div className="modal productModal">
      <div className="modal__inner">
        <div className="modal__close">
          <GrFormClose onClick={clearModal} />
        </div>
        <div className="productModal__info">
          <div className="productModal__imageContainer">
            <img
              src={productImgSrc}
              alt={productName}
              className="productModal__image"
            />
          </div>
          <h3 className="productModal__productName">{productName}</h3>
          <p className="productModal__productDescription">
            {productIngredients}
          </p>
        </div>
        <div className="productModal__orderList">
          {productPrices.map((pizzaPrice) => {
            const size = pizzaPrice?._embedded['wp:term'][0][0];
            let { price } = pizzaPrice.acf;
            price = price.slice(-3) === '.00' ? price.slice(0, -3) : price;
            price = price.replace('.', ',');
            const amountOfThisSize = productInCart.filter(
              (pizza) => pizza.size.id === pizzaPrice.acf.formaat
            ).length;

            return (
              <div className="productModal__order" key={size.slug}>
                <div className="productModal__orderCount">
                  {amountOfThisSize}
                </div>
                <div className="productModal__sizeName">{size.name}</div>
                <div className="productModal__orderCost">€{price}</div>
                <OrderControls
                  className="productModal__orderControls"
                  product={product}
                  pizzaPrice={pizzaPrice}
                />
              </div>
            );
          })}
        </div>
      </div>
      <div className="modal__bg" onClick={clearModal}></div>
    </div>
  );
};
