import React, { useEffect } from 'react';
import { Button, Header } from '../components';
import * as Routes from '../routes';
import { useHistory } from 'react-router-dom';

export const LoginWarningPage = () => {
  const history = useHistory();

  useEffect(() => {
    // TODO: validate via api.service.js
    const credentials = JSON.parse(localStorage.getItem('credentials'));
    // TODO: validate (POST: /jwt-auth/v1/token/validate)
    if (credentials) {
      history.push(Routes.PAY_METHOD);
    }
  }, [history]);

  return (
    <div className="page loginWarningPage">
      <Header type={Header.types.NORMAL} />
      <main className="container loginWarningPage__main">
        <h2 className="loginWarningPage__warningMessage">
          Log in om een bestelling te kunnen maken
        </h2>
        <div className="buttonGroup loginWarningPage__buttonGroup">
          <Button to={Routes.LOGIN} className="button--secondary">
            Login
          </Button>
          <Button to={Routes.REGISTER} className="button--secondary">
            Registreer
          </Button>
        </div>
      </main>
    </div>
  );
};
