import { default as React, useContext, createContext, useState } from 'react';
import { ProductModal } from '../components';

const ModalContext = createContext();
const useModal = () => useContext(ModalContext);

export const modalTypes = {
  PRODUCT: 0,
};

const ModalProvider = ({ children }) => {
  const [currentModal, setCurrentModal] = useState(-1);

  const clearModal = () => {
    setCurrentModal({ modalType: -1, props: {} });
  };

  /**
   * Set the current modal.
   * @param {number} modalType - The type of modal
   * @param {Object} props - The props to pass to the modal component
   */
  const setModal = (modalType, props = {}) => {
    setCurrentModal({ modalType, props });
  };

  const exports = {
    setModal,
    clearModal,
    modalTypes,
  };

  return (
    <ModalContext.Provider value={exports}>
      {currentModal.modalType === modalTypes.PRODUCT && (
        <ProductModal product={currentModal.props} />
      )}
      {children}
    </ModalContext.Provider>
  );
};

export { ModalContext, ModalProvider, useModal };
