import React from 'react';
import { Button, Header } from '../components';
import * as Routes from '../routes';
import { useApi } from '../services';
import { useHistory, useLocation } from 'react-router-dom';

export const LoginPage = () => {
  const { generateJWTToken } = useApi();
  const location = useLocation();
  const history = useHistory();

  const handleSubmit = async (event) => {
    event.preventDefault();
    const email = event.target['email'].value;
    const username = email;
    const password = event.target['password'].value;

    try {
      // Generate JWT
      await generateJWTToken(username, password);

      // TODO: validate via api.service.js
      const credentials = JSON.parse(localStorage.getItem('credentials'));
      // TODO: validate (POST: /jwt-auth/v1/token/validate)
      if (credentials) {
        if (
          location.pathname === Routes.LOGIN ||
          location.pathname === Routes.REGISTER
        ) {
          history.push(Routes.LANDING);
        }
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div className="page loginPage">
      <Header type={Header.types.WITH_HOME} />
      <main className="container loginPage__main">
        <form action="POST" onSubmit={handleSubmit} className="loginPage__form">
          <div className="input input--email">
            <label htmlFor="email">E-mail</label>
            <input
              type="email"
              name="email"
              id="email"
              placeholder="janjanssens@mail.com"
              required
            />
          </div>
          <div className="input input--password">
            <label htmlFor="password">Wachtwoord</label>
            <input type="password" name="password" id="password" required />
          </div>
          <div className="buttonGroup loginPage__buttonGroup">
            <Button>Login</Button>
            <Button to={Routes.REGISTER} className="button--textOnly">
              Registreer
            </Button>
          </div>
        </form>
      </main>
    </div>
  );
};
