import React, { useEffect, useState } from 'react';
import { Button, Header } from '../components';

import * as Routes from '../routes';

export const HomePage = () => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  // const { deleteCurrentUser } = useApi();

  useEffect(() => {
    const credentials = JSON.parse(localStorage.getItem('credentials'));
    // TODO: validate via api.service.js
    setIsLoggedIn(!!credentials);
  }, []);

  const logout = () => {
    localStorage.removeItem('credentials');
    setIsLoggedIn(false);
  };

  // const deleteAccount = () => {
  //   console.log('deleting account');
  //   // Delete customer_data
  //   // Delete account --> eerst proberen zonder customer_data te verwijderen (gebeurt automatisch?)
  //   deleteCurrentUser();
  //   console.log('account deleted');
  //   // Delete credentials in localStorage
  // };

  return (
    <div className="page homePage">
      <Header type={Header.types.LARGE} />

      <main
        className={`homePage__main ${
          isLoggedIn ? 'homePage__main--loggedIn' : ''
        }`}>
        {isLoggedIn ? (
          <>
            <div className="homePage__buttonGroup">
              <Button
                to={Routes.PRODUCTS}
                className="homePage__pizzaButton homePage__pizzaButton--loggedIn">
                Pizza's
              </Button>
              <Button to={Routes.ORDERS} className="button--secondary">
                Mijn Bestellingen
              </Button>
            </div>
            <div className="homePage__buttonGroup">
              <Button onClick={logout} className="button--textOnly">
                Logout
              </Button>
              {/* <Button onClick={deleteAccount} className="button--textOnly">
                Verwijder Account
              </Button> */}
            </div>
          </>
        ) : (
          <>
            <div className="homePage__buttonGroup">
              <Button to={Routes.PRODUCTS} className="homePage__pizzaButton">
                Pizza's
              </Button>
            </div>
            <div className="homePage__buttonGroup homePage__buttonGroup--loginRegister">
              <Button
                to={Routes.LOGIN}
                className="button--secondary"
              >
                Login
              </Button>
              <Button
                to={Routes.REGISTER}
                className="button--secondary"
              >
                Registreer
              </Button>
            </div>
          </>
        )}
      </main>
    </div>
  );
};
