import React from 'react';

export const Triangle = ({ className = '' }) => {
  return (
    <svg
      className={`triangle ${className}`}
      xmlns="http://www.w3.org/2000/svg"
      width="17"
      height="15"
      viewBox="0 0 17 15">
      <path id="A" className="triangle__path" d="M8.5,0,17,15H0Z" />
    </svg>
  );
};
