import React from 'react';
import { Button } from '../components';
import { useOrder } from '../services/order.service';
import * as Routes from '../routes';
import { useHistory } from 'react-router-dom';

export const PayPage = () => {
  const { placeOrder } = useOrder();
  const history = useHistory();

  const handleSubmit = async () => {
    await placeOrder();
    history.push(Routes.ORDERS);
  };

  return (
    <div className="page payPage">
      {/* <Header type={Header.types.NORMAL} /> */}
      <main className="container payPage__main">
        <div className="payPage__invisibleTop"></div>
        <h2 className="payPage__message">Betaal</h2>
        <Button onClick={handleSubmit}>Bevestig</Button>
      </main>
    </div>
  );
};
