import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Triangle } from '../../components';

export const Dropdown = ({
  options,
  selectedOption = null,
  placeholder,
  onChange,
}) => {
  const [showList, setShowList] = useState(false);

  const nameToShow = placeholder || selectedOption?.name || 'DEFAULT';
  const [selectedOpt, setSelectedOpt] = useState({ name: nameToShow });

  const toggleShowList = () => {
    setShowList((old) => !old);
  };

  const select = (option) => {
    setSelectedOpt(option);
    toggleShowList();
    if (onChange) onChange(option);
  };

  return (
    <div className="dropdown">
      <div
        className={`dropdown__main${showList ? ' dropdown__main--open' : ''}`}
        onClick={toggleShowList}>
        {selectedOpt.name}
        <Triangle />
      </div>
      <div
        className={`dropdown__list${showList ? '' : ' dropdown__list--hide'}`}>
        {options.map((option) => {
          return (
            <div
              key={option.value}
              className="dropdown__option"
              onClick={() => select(option)}>
              {option.name}
            </div>
          );
        })}
      </div>
    </div>
  );
};

Dropdown.propTypes = {
  /**
   * All options.
   *
   * @param {string} options[].name - Name of the option
   * @param {(string|number)} options[].value - Unique value of the option
   * @param {Object[]} options - List of options
   */
  options: PropTypes.array,
};
