import {
  default as React,
  useContext,
  createContext,
  useState,
  useEffect,
} from 'react';
import { useApi } from './api.service';

const OrderContext = createContext();
const useOrder = () => useContext(OrderContext);

const OrderProvider = ({ children }) => {
  const emptyOrder = {
    title: 'Nieuwe bestelling via de client',
    status: 'publish',
    fields: {
      pizzas: [
        {
          itemId: `${23}-${17}-${Math.random()}-${Date.now()}`,
          pizzaId: 23,
          pizzaName: 'Pizza Veggie',
          pizzaPriceId: 17,
          size: {
            id: 5,
            link: 'http://localhost:8080/size/medium/',
            name: 'Normaal',
            slug: 'medium',
            taxonomy: 'size',
          },
          price: 5,
        },
      ],
      // pizzas: [],
      address: 'straat nummer woonplaats',
      purchase: '1x Pizza Kip Groot €5.50; 2x Pizza Veggi Groot €11.00;',
      total: 16.5,
    },
  };

  const [currentOrder, setCurrentOrder] = useState(emptyOrder);
  const [uniquePizzasInCart, setUniquePizzasInCart] = useState([]);
  const { getCustomerData, postOrder } = useApi();

  useEffect(() => {
    let uniquePizzas = [];
    currentOrder.fields.pizzas.forEach((pizza) => {
      const foundPizza = uniquePizzas.find(
        (item) =>
          item.pizzaId === pizza.pizzaId &&
          item.pizzaPriceId === pizza.pizzaPriceId
      );

      if (foundPizza) {
        uniquePizzas = uniquePizzas.map((item) => {
          if (item.itemId === foundPizza.itemId) {
            // console.log('adjust item', foundPizza);
            return {
              ...foundPizza,
              amount: foundPizza.amount + 1,
            };
          }
          return item;
        });
        // console.log(uniquePizzas);
      } else {
        // console.log('other item');
        uniquePizzas.push({
          ...pizza,
          amount: 1,
        });
        // console.log(uniquePizzas);
      }
    });
    // console.log(uniquePizzas);
    setUniquePizzasInCart(uniquePizzas);
  }, [currentOrder]);

  const clearCart = () => {
    setCurrentOrder(emptyOrder);
  };

  const addProductToCart = ({ product, pizzaPrice }) => {
    // Add product to order
    // console.log(pizzaPrice);
    const size = pizzaPrice?._embedded['wp:term'][0][0];
    let { price } = pizzaPrice.acf;
    price = parseFloat(price);

    setCurrentOrder((old) => ({
      ...old,
      fields: {
        ...old.fields,
        pizzas: [
          ...old.fields.pizzas,
          {
            itemId: `${product.id}-${
              pizzaPrice.id
            }-${Math.random()}-${Date.now()}`,
            pizzaId: product.id,
            pizzaName: product.title.rendered,
            pizzaPriceId: pizzaPrice.id,
            size,
            price,
          },
        ],
      },
    }));
  };

  const duplicateCartItem = (duplicateItemId) => {
    const generateItemId = (pizza) => {
      return `${pizza?.pizzaId}-${
        pizza?.pizzaPriceId
      }-${Math.random()}-${Date.now()}`;
    };

    setCurrentOrder((old) => ({
      ...old,
      fields: {
        ...old.fields,
        pizzas: [
          ...old.fields.pizzas,
          {
            ...old.fields.pizzas.find(
              (pizza) => pizza.itemId === duplicateItemId
            ),
            itemId: generateItemId(
              old.fields.pizzas.find(
                (pizza) => pizza.itemId === duplicateItemId
              )
            ),
          },
        ],
      },
    }));
  };

  /**
   * Remove a pizza from the cart.
   * @param {object} pizza - Pizza details
   * @param {number} pizza.pizzaId - Id of the pizza to remove
   * @param {number} pizza.pizzaPriceId - Id of the pizzaPrice of the pizza to remove
   */
  const removeProductFromCart = ({ pizzaId, pizzaPriceId }) => {
    const otherPizzas = (pizzaArray) => {
      const newPizzaArray = pizzaArray.filter(
        (pizza) =>
          !(pizza.pizzaId === pizzaId && pizza.pizzaPriceId === pizzaPriceId)
      );
      return newPizzaArray;
    };

    const oneLessOfSpecificPizza = (pizzaArray) => {
      const newPizzaArray = pizzaArray.filter(
        (pizza) =>
          pizza.pizzaId === pizzaId && pizza.pizzaPriceId === pizzaPriceId
      );
      newPizzaArray.pop();
      return newPizzaArray;
    };

    setCurrentOrder((old) => ({
      ...old,
      fields: {
        ...old.fields,
        pizzas: [
          ...otherPizzas(old.fields.pizzas),
          ...oneLessOfSpecificPizza(old.fields.pizzas),
        ],
      },
    }));
  };

  const placeOrder = async () => {
    const credentials = JSON.parse(localStorage.getItem('credentials'));
    const customerData = (await getCustomerData(credentials.user_id))[0];
    // Format order
    let total = 0;
    const orderStrings = uniquePizzasInCart.map((pizza) => {
      const pizzaTotal = pizza.amount * pizza.price;
      total += pizzaTotal;
      return `${pizza.amount}x ${pizza.pizzaName} ${pizza.size.name} = €${pizzaTotal}`;
    });
    const address = `${customerData.acf.street} ${customerData.acf.housenumber} ${customerData.acf.postalcode}`;
    const deliveryTime = new Date(Date.now() + 60 * 60 * 1000);
    const data = {
      customer_data: customerData.id,
      purchase: `Bestelling voor ${customerData.acf.firstname} ${
        customerData.acf.lastname
      }: ${orderStrings.join('; ')}`,
      total,
      address,
      // Current time + 1 hour
      delivery_time: `${deliveryTime
        .toTimeString()
        .split(' ')[0]
        .slice(0, -3)}`,
    };

    // Clear currentOrder
    setCurrentOrder(emptyOrder);

    // POST order
    return postOrder(data);
  };

  const exports = {
    clearCart,
    addProductToCart,
    duplicateCartItem,
    removeProductFromCart,
    currentOrder,
    uniquePizzasInCart,
    placeOrder,
  };

  return (
    <OrderContext.Provider value={exports}>{children}</OrderContext.Provider>
  );
};

export { OrderContext, OrderProvider, useOrder };
