import React from 'react';
import { Button, Header, Order } from '../components';
import * as Routes from '../routes';
import { useHistory } from 'react-router-dom';

export const CartPage = () => {
  const history = useHistory();

  const handleConfirm = () => {
    const credentials = JSON.parse(localStorage.getItem('credentials'));
    // TODO: validate
    if (!credentials) {
      // Send to loginWarning
      history.push(Routes.LOGIN_WARNING);
      return;
    }
    // Send to Pay method
    history.push(Routes.PAY_METHOD);
  };
  return (
    <div className="page cartPage">
      <Header type={Header.types.NORMAL} />
      <main className="container cartPage__main">
        <Order isCart={true} className="cartPage__order" />
        <div className="carpage__buttonGroup buttonGroup">
          <Button onClick={handleConfirm}>Bevestig</Button>
          <Button to={Routes.PRODUCTS} className="button--textOnly">
            Terug
          </Button>
        </div>
      </main>
    </div>
  );
};
