import React from 'react';
import { useModal, modalTypes } from '../../services';

export const ProductCard = ({ product }) => {
  const productName = product?.title?.rendered || '';
  const productImgSrc =
    product?._embedded['wp:featuredmedia'][0].source_url || '';
  const { setModal } = useModal();

  const handleClick = () => {
    setModal(modalTypes.PRODUCT, product);
  };

  return (
    <div className="productCard" onClick={handleClick}>
      <div className="productCard__imageContainer">
        <img
          className="productCard__image"
          src={productImgSrc}
          alt={productName}
        />
      </div>
      <div className="productCard__name">{productName}</div>
    </div>
  );
};

export * from './ProductList';
