import React from 'react';
import { Button, Header } from '../components';
import * as Routes from '../routes';

export const PayMethodPage = () => {
  return (
    <div className="page payMethodPage">
      <Header type={Header.types.NORMAL} />
      <main className="container payMethodPage__main">
        <h2 className="payMethodPage__message">Kies uw betaalmethode</h2>
        <div className="buttonGroup payMethodPage__buttonGroup">
          <Button to={Routes.PAY}>Bancontact</Button>
          <Button to={Routes.PAY}>PayPal</Button>
        </div>
        <Button to={Routes.CART} className="button--textOnly">
          Terug
        </Button>
      </main>
    </div>
  );
};
