import React from 'react';
import { Button, Header } from '../components';
import * as Routes from '../routes';
import { useApi } from '../services';

export const RegisterPage = () => {
  const { registerUser, createCustomerData, generateJWTToken } = useApi();

  const handleSubmit = async (event) => {
    event.preventDefault();
    const firstname = event.target['firstName'].value;
    const lastname = event.target['lastName'].value;
    const username = event.target['email'].value;

    const postalcode = event.target['postalCode'].value;
    const street = event.target['street'].value;
    const housenumber = event.target['houseNumber'].value;
    const password = event.target['password'].value;

    try {
      // Register account
      const newUser = await registerUser(
        username,
        event.target['email'].value,
        password
      );

      // Generate JWT
      await generateJWTToken(username, password);

      // Create Customer Data
      await createCustomerData({
        username,
        firstname,
        lastname,
        postalcode,
        street,
        housenumber,
        customer: newUser.id,
      });
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div className="page registerPage">
      <Header type={Header.types.WITH_HOME} />
      <main className="container registerPage__main">
        <form
          action="POST"
          onSubmit={handleSubmit}
          className="registerPage__form">
          <div className="input input--text">
            <label htmlFor="firstName">Voornaam</label>
            <input
              type="text"
              name="firstName"
              id="firstName"
              placeholder="Jan"
              required
            />
          </div>
          <div className="input input--text">
            <label htmlFor="lastName">Achternaam</label>
            <input
              type="text"
              name="lastName"
              id="lastName"
              placeholder="Janssens"
            />
          </div>
          <div className="input input--email">
            <label htmlFor="email">E-mail</label>
            <input
              type="email"
              name="email"
              id="email"
              placeholder="janjanssens@mail.com"
              required
            />
          </div>
          <div className="input input--postalCode">
            <label htmlFor="postalCode">Postcode</label>
            <input
              type="test"
              name="postalCode"
              id="postalCode"
              placeholder="9000"
              required
            />
          </div>
          <div className="registerPage__streetNumberGroup">
            <div className="input input--street">
              <label htmlFor="street">Straat</label>
              <input
                type="test"
                name="street"
                id="street"
                placeholder="Korenmarkt"
                required
              />
            </div>
            <div className="input input--houseNumber">
              <label htmlFor="houseNumber">Huisnummer</label>
              <input
                type="test"
                name="houseNumber"
                id="houseNumber"
                placeholder="1"
                required
              />
            </div>
          </div>
          <div className="input input--password">
            <label htmlFor="password">Kies een wachtwoord</label>
            <input
              type="password"
              name="password"
              id="password"
              required
            />
          </div>
          <div className="buttonGroup registerPage__buttonGroup">
            <Button>Registreer</Button>
            <Button to={Routes.LOGIN} className="button--textOnly">
              Login
            </Button>
          </div>
        </form>
      </main>
    </div>
  );
};
