import { useEffect } from 'react';
import { Route as ReactRoute, useHistory, useLocation } from 'react-router';
import { BrowserRouter as Router, Redirect, Switch } from 'react-router-dom';
import { CartPage, HomePage, LoginPage, LoginWarningPage, OrdersPage, PayMethodPage, PayPage, ProductsPage, RegisterPage } from '../../pages';
import * as Routes from '../../routes';
import { useModal } from '../../services';

const RouteListener = ({ children }) => {
  const location = useLocation();
  const history = useHistory();
  const { clearModal } = useModal();

  useEffect(() => {
    clearModal();
    // TODO: validate via api.service.js
    const credentials = JSON.parse(localStorage.getItem('credentials'));
    // TODO: validate (POST: /jwt-auth/v1/token/validate)
    if (credentials) {
      if (location.pathname === Routes.LOGIN || location.pathname === Routes.REGISTER) {
        history.push(Routes.LANDING);
      }
    }
  }, [location, history]);

  return <>{children}</>;
};

export function App() {
  return (
    <div className="app">
      <Router basename="/pizzabyte-client/">
        <RouteListener />
        <Switch>
          <ReactRoute exact path={Routes.LANDING} component={HomePage} />
          <Redirect from={Routes.HOME} to={Routes.LANDING} />
          <ReactRoute exact path={Routes.PRODUCTS} component={ProductsPage} />
          <ReactRoute exact path={Routes.CART} component={CartPage} />
          <ReactRoute exact path={Routes.REGISTER} component={RegisterPage} />
          <ReactRoute exact path={Routes.LOGIN} component={LoginPage} />
          <ReactRoute exact path={Routes.LOGIN_WARNING} component={LoginWarningPage} />
          <ReactRoute exact path={Routes.PAY_METHOD} component={PayMethodPage} />
          <ReactRoute exact path={Routes.PAY} component={PayPage} />
          <ReactRoute exact path={Routes.ORDERS} component={OrdersPage} />
        </Switch>
      </Router>
    </div>
  );
}
