import React from 'react';
import { useHistory } from 'react-router-dom';

export const Button = ({ className = '', children, to, onClick, ...rest }) => {
  const history = useHistory();

  const clickHandler = (e) => {
    if (onClick) onClick(e);
    if (!to) return;
    history.push(to);
  }

  return (
    <button type="submit" className={`button ${className}`} onClick={clickHandler} {...rest}>
      {children}
    </button>
  );
};
