import React, { useEffect, useState } from 'react';
import { Order } from '.';
import { useApi } from '../../services';

export const OrderList = ({ className = '' }) => {
  const { getAllOrdersFromUser } = useApi();
  const [allOrders, setAllOrders] = useState([]);

  useEffect(() => {
    (async () => {
      const userId = JSON.parse(localStorage.getItem('credentials')).user_id;
      const orders = await getAllOrdersFromUser(userId);
      setAllOrders(orders);
      console.log(userId, orders);
      return orders;
    })();
    return;
  }, [getAllOrdersFromUser]);

  return (
    <div className={`orderList ${className}`}>
      {allOrders.map((order) => (
        <Order key={order.id} extendable={true} order={order} />
      ))}
    </div>
  );
};
