import React, { useEffect, useState } from 'react';
import { useOrder } from '../../services/order.service';
import { OrderControls } from '../Controls';
import { Triangle } from '../Triangle';

export const Order = ({
  extendable = false,
  isCart = false,
  className = '',
  order,
}) => {
  const { uniquePizzasInCart } = useOrder();
  const [totalPrice, setTotalPrice] = useState(0);
  const [isExtended, setIsExtended] = useState(false);

  useEffect(() => {
    if (uniquePizzasInCart?.length < 1 || !isCart) return;
    const total = uniquePizzasInCart
      .map((pizza) => pizza.amount * pizza.price)
      .reduce((prev, currPizza) => prev + currPizza);
    setTotalPrice(total);
  }, [uniquePizzasInCart, isCart]);

  useEffect(() => {
    if (!order) return;
    setTotalPrice(order.acf.total);
  }, [order]);

  const handleOnClick = () => {
    if (!extendable) return;
    setIsExtended((old) => !old);
  };

  return (
    <div className={`order ${className}`}>
      <div className="order__content" onClick={handleOnClick}>
        {isCart &&
          uniquePizzasInCart.map((pizza) => {
            let { size, price, amount, pizzaName, itemId } = pizza;

            return (
              <div key={itemId} className="orderElement">
                <div className="orderElement__orderAmount">{amount}</div>
                <div className="orderElement__uniquePizza">
                  <div className="orderElement__pizzaName">{pizzaName}</div>
                  <div className="orderElement__pizzaSize">
                    {size.name}
                    <div className="orderElement__pizzaCost">€{price}</div>
                  </div>
                </div>
                <OrderControls
                  className="orderElement__orderControls"
                  product={pizza}
                  duplicateItemId={pizza.itemId}
                />
              </div>
            );
          })}
        {extendable && (
          <>
            <div className="order__header">
              <div className="order__date">
                {new Date(order.date).toLocaleString('nl').split(' ')[0]}
              </div>
              <div className={`order__arrowContainer ${isExtended ? 'order__arrowContainer--extended' : ''}`}>
                <Triangle className="order__arrow" />
              </div>
            </div>
            <div
              className={`order__collapseArea ${
                isExtended ? '' : 'order__collapseArea--closed'
              }`}>
              {order.acf.purchase}
            </div>
          </>
        )}
      </div>
      <div
        className={`order__footer ${
          isExtended ? '' : 'order__footer--closed'
        }`}>
        <div className="order__total">
          <div className="order__totalLabel">Totaal</div>
          <div className="order__totalAmount">€{totalPrice}</div>
        </div>
        <div className="order__deliveryTime">
          <div className="order__deliveryTimeLabel">Levering: </div>
          <div className="order__deliveryTimeHour">
            {isCart ? 'Binnen ongeveer 1 uur' : `om ${order.acf.delivery_time}`}
          </div>
        </div>
      </div>
    </div>
  );
};
