import React from 'react';
import { Logo } from '../Logo';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import * as Routes from '../../routes';
import { MdHome } from "react-icons/md";


const headerTypes = {
  NORMAL: 0,
  WITH_HOME: 1,
  LARGE: 2,
};

export const Header = ({ type }) => {
  let extraHeaderClassNames = '';
  let extraLogoClassNames = '';
  let extraChildren = <></>;

  switch (type) {
    case headerTypes.LARGE:
      extraHeaderClassNames = ' header--large';
      extraLogoClassNames = 'logo--large';
      break;
    case headerTypes.WITH_HOME:
      extraHeaderClassNames = ' header--withHome';
      extraChildren = <Link to={Routes.LANDING} className="header__homeButton" ><MdHome /></Link>;
      break;

    default:
      break;
  }
  return (
    <header className={`header${extraHeaderClassNames} container`}>
      <Logo className={extraLogoClassNames} />
      {extraChildren}
    </header>
  );
};

Header.types = headerTypes;

Header.propTypes = {
  /**
   * Type of header.
   */
  type: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.oneOf(Object.keys(headerTypes)),
  ]),
};
