import React, { useEffect, useState } from 'react';
import { Button, Dropdown, Header, ProductList } from '../components';
import { useOrder } from '../services/order.service';
import * as Routes from '../routes';
import { useApi } from '../services';

export const ProductsPage = () => {
  const { currentOrder } = useOrder();
  const { getAllCategories, getAllProducts } = useApi();
  const [allCategories, setAllCategories] = useState([]);
  const [allProducts, setAllProducts] = useState([]);

  const fetchCategories = async () => {
    const allCats = await getAllCategories();
    let allRealCats = allCats.filter(
      (category) => category.name !== 'Geen categorie'
    );
    allRealCats = allRealCats.map((category) => ({
      name: category.name,
      value: category.id,
    }));
    allRealCats.unshift({
      name: 'Alle',
      value: '-1',
    });
    setAllCategories(allRealCats);
    return allRealCats;
  };

  const fetchProducts = async (categoryId = null) => {
    const allProds = await getAllProducts(true, categoryId);
    setAllProducts(allProds);
    return allProds;
  };

  useEffect(() => {
    fetchProducts();
    fetchCategories();
  }, []);

  const handleCategoryChange = (event) => {
    fetchProducts(event.value);
  };

  return (
    <div className="page productsPage">
      <Header type={Header.types.WITH_HOME} />
      <main className="container productsPage__main">
        <section className="productsPage__filterSection">
          <Dropdown
            options={allCategories}
            placeholder="Category"
            onChange={handleCategoryChange}
          />
        </section>
        <section className="productsPage__results">
          <ProductList allProducts={allProducts} />
        </section>
      </main>
      {currentOrder?.fields?.pizzas?.length > 0 && (
        <div className="center">
          <Button to={Routes.CART} className="button--secondary button--order">
            Bestel{` (${currentOrder?.fields?.pizzas?.length})`}
          </Button>
        </div>
      )}
    </div>
  );
};
